<template>
  <div :class="$root.themeHomeChange === '1' ? 'planEntry_light' : 'planEntry'">
    <div class="planEntry-con">
      <div class="headBox">
        <span class="title"> 计划执行 </span>
        <bda-button v-if="stepNum === 1" @click="goBack">关闭</bda-button>
        <bda-button v-else @click="stepNum--">返回上一步</bda-button>
      </div>
      <div class="content">
        <div class="formList" >
          <el-form
            :model="form"
            :rules="rules[stepNum]"
            :key="stepNum"
            ref="form"
            label-width="120px"
            label-suffix="："
            label-position="left"
            v-loading="formLoading"
            element-loading-text="拼命加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
          >
            <el-row :gutter="30" v-show="stepNum === 1">
              <el-col :span="5">
                <bda-form-item prop="year">
                  <el-date-picker
                    v-model="form.year"
                    type="year"
                    placeholder="请选择年度"
                    value-format="yyyy"
                    style="margin-right: 15px;"
                    @change="queryJdjhList(true)"
                  >
                  </el-date-picker>
                </bda-form-item>
              </el-col>
              <el-col :span="7">
                <bda-form-item prop="supervisionType">
                  <bda-select
                    v-model="form.supervisionType"
                    size="small"
                    filterable
                    clearable
                    placeholder="请选择监督类型"
                    @change="queryJdjhList(true)"
                    style="width: 100%"
                  >
                    <bda-option
                      v-for="(item, index) in options['supervisionType']"
                      :label="item.label"
                      :value="item.value"
                      :key="index"
                    ></bda-option>
                  </bda-select>
                </bda-form-item>
              </el-col>
              <el-col :span="12">
                <bda-form-item prop="jdjhId">
                  <bda-select
                    v-model="form.jdjhId"
                    size="small"
                    filterable
                    clearable
                    :disabled="!(form.year && form.supervisionType)"
                    :placeholder="(form.year && form.supervisionType) ? '请选择监督项目' : '请先选择年度及监督项目类型'"
                    style="width: 100%"
                    @change="getJdnrByjdrw"
                  >
                    <bda-option
                      v-for="(item, index) in options['jdjhId']"
                      :label="item.label"
                      :value="item.value"
                      :key="index"
                    ></bda-option>
                  </bda-select>
                </bda-form-item>
              </el-col>
              <el-col :span="24" style="height: 100px;">
                <bda-form-item label="监督内容" prop="jdnr">
                  <el-input
                    resize="none"
                    type="textarea"
                    :autosize="{ minRows: 3, maxRows: 3}"
                    placeholder="请选择监督项目"
                    disabled
                    v-model="form.jdnr">
                  </el-input>
                </bda-form-item>
              </el-col>
              <el-col :span="24" style="height: auto!important;">
                <bda-form-item label="计划开展内容" prop="jhkznr">
                  <div class="swiperBox">
                    <div
                      class="itemBox"
                      v-for="(item, index) in form.jhkznr"
                      :key="index"
                    >
                      <span class="input_box">
                        <el-input
                          resize="none"
                          type="textarea"
                          :autosize="{ minRows: 1, maxRows: 1}"
                          :placeholder="'请输入内容 <' + (index + 1) + '>'"
                          v-model="form.jhkznr[index]">
                        </el-input>
                      </span>
                      <span
                        class="btn_box"
                        v-if="form.jhkznr.length === (index + 1)"
                      >
                        <el-button
                          @click="form.jhkznr.push('')"
                          :plain="$root.themeHomeChange === '1'">增加</el-button>
                        <el-button
                          @click="form.jhkznr.pop()"
                          type="danger"
                          :plain="$root.themeHomeChange === '1'"
                          :disabled="form.jhkznr.length <= 1"
                        >删除</el-button>
                      </span>
                    </div>
                  </div>
                </bda-form-item>
              </el-col>
              <el-col :span="24" style="height: auto!important;">
                <bda-form-item label="发现问题" prop="question">
                  <div class="swiperBox">
                    <div
                      class="itemBox"
                      v-for="(item, index) in form.question"
                      :key="index"
                    >
                      <span class="input_box">
                        <el-input
                          resize="none"
                          type="textarea"
                          :autosize="{ minRows: 1, maxRows: 1}"
                          :placeholder="'请输入内容 <' + (index + 1) + '>'"
                          v-model="form.question[index]">
                        </el-input>
                      </span>
                      <span
                        class="btn_box"
                        v-if="form.question.length === (index + 1)"
                      >
                        <el-button
                          @click="form.question.push('')"
                          :plain="$root.themeHomeChange === '1'">增加</el-button>
                        <el-button
                          @click="form.question.pop()"
                          type="danger"
                          :plain="$root.themeHomeChange === '1'"
                          :disabled="form.question.length <= 1"
                        >删除</el-button>
                      </span>
                    </div>
                  </div>
                </bda-form-item>
              </el-col>
              <el-col :span="24">
                <bda-form-item label="问题类别" prop="questionType">
                  <el-input
                    placeholder="请输入问题类别"
                    v-model="form.questionType"
                    clearable
                    size="small">
                  </el-input>
                </bda-form-item>
              </el-col>
              
              <el-col :span="12">
                <bda-form-item label="责任单位" prop="zrdw">
                  <el-input
                    placeholder="请输入责任单位"
                    v-model="form.zrdw"
                    clearable
                    size="small">
                  </el-input>
                </bda-form-item>
              </el-col>
              <el-col :span="12">
                <bda-form-item label="责任人" prop="zrr">
                  <el-input
                    placeholder="请输入责任人"
                    v-model="form.zrr"
                    clearable
                    size="small">
                  </el-input>
                </bda-form-item>
              </el-col>
              
              <el-col :span="24" style="height: 100px;">
                <bda-form-item label="整改措施" prop="zgcs">
                  <el-input
                    resize="none"
                    type="textarea"
                    :autosize="{ minRows: 3, maxRows: 3}"
                    placeholder="请输入整改措施"
                    v-model="form.zgcs">
                  </el-input>
                </bda-form-item>
              </el-col>
            </el-row>
            
            <el-row :gutter="30" v-show="stepNum === 2">
              <el-col :span="24" style="height: auto;">
                <bda-form-item label="计划整改情况" prop="jhzgqk">
                  <el-input
                    resize="none"
                    type="textarea"
                    :autosize="{ minRows: 1, maxRows: 3}"
                    placeholder="请输入计划整改情况"
                    v-model="form.jhzgqk">
                  </el-input>
                </bda-form-item>
              </el-col>
              <el-col :span="24" style="height: auto;">
                <bda-form-item
                  label="上传附件: "
                  class="formItem-right"
                  label-width="200px"
                  prop="filePath"
                >
                  <el-upload
                    ref="uploadObj"
                    :action="fileUrl + form.jdjhId"
                    list-type="picture-card"
                    :on-success="handleSuccess"
                    :on-error="handleError"
                    :file-list="form.fileList"
                    :auto-upload="true"
                    accept=".xls, .xlsx, .docx, .pdf"
                    :before-upload="beforeUpload"
                    :limit="1"
                    style="display: flex; flex-wrap: wrap;"
                    :on-exceed="handleExceed">
                    <i slot="default" class="el-icon-plus">
                      <p>上传附件</p>
                    </i>
                    <div slot="file" slot-scope="{file}">
                      <div class="fileClass">
                        <i class="el-icon-document">
                          <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                            <span >
                              <p>{{file.name}}</p>
                            </span>
                          </el-tooltip>
                        </i>
                        <el-popconfirm
                          :popper-class="$root.themeHomeChange === '1' ? 'popconfirmClass_light' : 'popconfirmClass'"
                          confirm-button-text='确定'
                          cancel-button-text='取消'
                          icon="el-icon-info"
                          icon-color="red"
                          :title="`是否删除 ${file.name} ？`"
                          @confirm="deleteUploadItem(file)"
                        >
                          <i slot="reference" class="deleteIcon el-icon-circle-close"></i>
                        </el-popconfirm>
                      </div>
                    </div>
                  </el-upload>
                  <p style="color: #bbb; font-size: 10px;">文件后缀名可为xls 或xlsx （即Excel格式）、word、pdf等，文件大小不得大于30M。</p>
                </bda-form-item>
              </el-col>
              <el-col :span="12">
                <bda-form-item label="是否完成整改" prop="isCompletedRectify" style="padding: 0 15px;">
                  <el-radio-group
                    v-model="form.isCompletedRectify"
                    @change="() => {
                      form.unfinishedRectifyReason = ''
                    }"
                  >
                    <el-radio :label="1">是</el-radio>
                    <el-radio :label="0">否</el-radio>
                  </el-radio-group>
                </bda-form-item>
              </el-col>
              <el-col :span="24" style="height: auto;" v-if="form.isCompletedRectify === 0" >
                <bda-form-item label="未完成整改原因说明" prop="unfinishedRectifyReason">
                  <el-input
                    resize="none"
                    type="textarea"
                    :autosize="{ minRows: 1, maxRows: 3}"
                    placeholder="请输入未完成整改原因说明"
                    v-model="form.unfinishedRectifyReason">
                  </el-input>
                </bda-form-item>
              </el-col>
              <el-col :span="12">
                <bda-form-item label="整改完成时间" prop="completedRectifyDate">
                  <el-input-number
                    v-model="form.completedRectifyDate"
                    style="width: 100%;"
                    :min="1"
                    :max="12"
                    size="mini"
                    placeholder="请输入整改完成时间(月份)"
                  ></el-input-number>
                </bda-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        
        <div class="submit" >
          <el-button v-if="stepNum === 1" @click="nextStep()" type="primary"> 下一步 </el-button>
          <span v-else>
            <el-button @click="submit()" type="primary"> 提交 </el-button>
            <el-button @click="goBack()"> 取消 </el-button>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from "@/base/mixin/risk-control-center.js";
// import qs from "qs";
import moment from "moment";
  import { API_PATH } from '@/config';

export default {
  components: {},
  mixins: [Interface],
  props: {
    formData_in: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    var check_jhkznr = (rule, value, callback) => {
      if (value.length !== 0) {
        if(value.some(item => {
            return item.trim() == '';
          })
        ) {
          return callback(new Error('请填写完整计划开展内容'));
        } else {
          return callback();
        }
      } else {
        return callback(new Error('数据出现错误，请出现打开'));
      }
    };
    var check_question = (rule, value, callback) => {
      if (value.length !== 0) {
        if(value.some(item => {
            return item.trim() == '';
          })
        ) {
          return callback(new Error('请填写完整发现问题内容'));
        } else {
          return callback();
        }
      } else {
        return callback(new Error('数据出现错误，请出现打开'));
      }
    };
    return {
      // 表单加载
      formLoading: false,
      fileUrl: API_PATH + '/supervision/exec/file/upload?jdrwId=',
      // 表单步骤，（ 1，第一步，2是第二步 ）
      stepNum: 1,
      form: {
        jhkznr: [''],
        question: [''],
        questionType: '',
        year: '',
        jdjhId: '',
        jdnr: '',
        unfinishedRectifyReason: '',
        fileList: []
      },
      
      detailInfo: [], // 详细信息
      
      keyValue: '', //关键字
      
      chartData: {}, // 图表数据
      
      infoLoading: false,  // 基本信息加载
      
      infoData: {}, // 基本信息
      
      options: {
        supervisionType: [
          {
            label: '职能监督',
            value: '职能监督'
          },
          {
            label: '专业监督',
            value: '专业监督'
          },
          {
            label: '专责监督',
            value: '专责监督'
          },
          {
            label: '安全监督',
            value: '安全监督'
          },
          {
            label: '基层单位日常监督',
            value: '基层单位日常监督'
          }
        ],
        questionType: [
          {
            label: '职能监督',
            value: '职能监督'
          },
          {
            label: '专业监督',
            value: '专业监督'
          },
          {
            label: '专责监督',
            value: '专责监督'
          },
          {
            label: '安全监督',
            value: '安全监督'
          },
          {
            label: '基层单位日常监督',
            value: '基层单位日常监督'
          }
        ],
        jdjhId: [],
      },
      
      rules: {
        1: {
          // jdnr: [
          //   { required: true, message: '请输入监督内容', trigger: 'blur' },
          //   // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          // ],
          jhkznr: [
            { required: true, validator: check_jhkznr, trigger: 'blur' }
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          question: [
            { required: true, validator: check_question, trigger: 'blur' }
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          questionType: [
            { required: true, message: '请输入问题类别', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          zgcs: [
            { required: true, message: '请输入整改措施', trigger: 'blur' },
          ],
          jdjhId: [
            { required: true, message: '请选择监督项目', trigger: 'change' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
        },
        2: {
          jhzgqk: [
            { required: true, message: '请输入计划整改情况', trigger: 'blur' },
          ],
          filePath: [
            { required: true, message: '请上传附件', trigger: 'change' },
          ],
          isCompletedRectify: [
            { required: true, message: '请选择是否完成整改', trigger: 'change' },
          ],
          completedRectifyDate: [
            { required: true, message: '请输入整改完成时间（月份）', trigger: 'change' },
          ],
          unfinishedRectifyReason: [
            { required: true, message: '请输入未完成整改原因说明', trigger: 'blur' },
          ],
        }
      },
    };
  },
  created() {},
  mounted() {
  },
  watch: {
    'form.isCompletedRectify': {
      handler(v) {
        if(this.stepNum === 2) {
          this.rules[this.stepNum]['unfinishedRectifyReason'][0].required = (v === 1 ? false : true);
        }
      },
      immediate: false,
      deep: true
    },
    'form.fileList': {
      handler(v) {
        // console.log('深度监听', v);
        this.form.filePath = Array.isArray(v) && v.length !== 0 ? v[0].url : '';
      },
      deep: true
    },
    formData_in: {
      handler(v) {
        // window.console.log(v);
        if(v && v.jdjhId && v.jdrwId) {
          let type = '', year = '';
          this.getSupervisionDetails({ id: v.jdjhId }).then((rs)=>{
            if(rs && rs.id) {
              year = rs.nd;
              type = rs.type
            }
          }).finally(()=>{})
          
          setTimeout(() => {
            this.form = JSON.parse(JSON.stringify({
              ...v,
              year,
              supervisionType: type,
              fileList: [
                {
                  name: this.getFileName(v.filePath),
                  url: v.filePath
                }
              ],
              jhkznr: (Array.isArray(v.jhkznrList) && v.jhkznrList.length !== 0) ? v.jhkznrList.map(item => item.jhkznr) : [''],
              question: (Array.isArray(v.questionList) && v.questionList.length !== 0) ? v.questionList.map(item => item.question) : ['']
            }));
            this.queryJdjhList(false)
          }, 500);
          
        } else {
          this.form = {
            jhkznr: [''],
            question: [''],
            questionType: '',
            year: new Date().getFullYear().toString(),
            jdjhId: '',
            jdnr: '',
            unfinishedRectifyReason: '',
            fileList: []
          };
        }
      },
      immediate: true,
      deep: true
    },
  },
  methods: {
    // 下一步
    nextStep() {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.stepNum++;
          window.console.log(this.stepNum, this.form)
        } else {
          return false;
        }
      });
    },
    // 格式化数据
    formatDate(time, type) {
      return time ? moment(time).format(type) : ' / ';
    },
    
    // 删除上传列表
    deleteUploadItem(file) {
      // window.console.log(file, this.formData.fileList);
      let uploadFiles = this.$refs['uploadObj'].uploadFiles;
      
      // 清空组件中的数据
      uploadFiles.splice(uploadFiles.indexOf(file), 1);
      // 清空列表中的数据
      // window.console.log(this.formData.fileList);
      this.form.fileList.splice(this.form.fileList.indexOf(file), 1);
      // let index = this.formData.fileList.some((item, index) => {
      //   if(item.name === file.name){
      //     return index + 1;
      //   }
      // })
      // this.formData.fileList.splice(index, 1)
    },
    
    // 查询监督计划列表数据
    queryJdjhList(isReset = false) {
      let params = {
        type:  this.form.supervisionType,
        year: this.form.year,
        usepage: false
      };
      this.getSupervisionList(params).then((rs)=>{
        // window.console.log(rs)
        if(rs) {
          this.options['jdjhId'] = rs.rows.map( item => {
            return {
              label: item.jdxmmc,
              value: item.id,
              jdnr: item.jdnr
            }
          })
        } else {
          this.options['jdjhId'] = [];
        }
      }).finally(()=>{
        if(isReset) {
          this.form.jdjhId = '';
          this.form.jdnr = '';
        }
      })
    },
    
    // 通过监督项目获取监督内容
    getJdnrByjdrw(id) {
      let item = this.options['jdjhId'].find(item => item.value === id);
      this.form.jdnr =  item && item.jdnr;
    },
    
    getFileName(path) {
      let pos1 = path.lastIndexOf('/'); 
      let pos2 = path.lastIndexOf('\\'); 
      let pos = Math.max(pos1, pos2); 
      if (pos < 0) { 
        return path; 
      } 
      else { 
        return path.substring(pos + 1); 
      } 
    },
    
    // 提交表单
    submit() {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.$confirm('此操作将执行当前设置的监督计划, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.formLoading = true;
            this[this.form.jdrwId ? 'supervisionExecUpdate' : 'supervisionExecAdd'](this.form).then(()=>{
              this.$message.success('提交成功');
              this.goBack();
            }).finally(()=>{
              this.formLoading = false;
            });
          }).catch(() => {});
        } else {
          return false;
        }
      });
    },
    
    // 表单上传组件
    // 上传校验
    beforeUpload(file) {
      window.console.log(file, '上传校验')
      let testmsg = file.name.substring(file.name.lastIndexOf('.')+1)
      const extension = ['xls', 'xlsx', 'docx', 'pdf'];
      const isLt2M = file.size / 1024 / 1024 < 30
      
      if(!extension.includes(testmsg)) {
          this.$message({
              message: '上传文件只能是 xls、xlsx、docx、pdf格式!',
              type: 'warning'
          });
      }
      if(!isLt2M) {
          this.$message({
              message: '上传文件大小不能超过 30MB!',
              type: 'warning'
          });
      }
      return extension.includes(testmsg) && isLt2M;
    },
    handleRemove(file, fileList) {
      window.console.log(file, fileList);
    },
    handlePreview(file) {
      window.console.log(file);
    },
    // 上传失败
    handleError() {
      this.$message.error(`附件上传失败 !`);
    },
    // 上传超出限制
    handleExceed() {
      this.$message.warning(`附件上传超出限制个数 !`);
    },
    // 上传成功
    handleSuccess(file, fileList) {
      // window.console.log(file, fileList, Object.values(fileList.response.data));
      if(Array.isArray(this.form.fileList) && this.form.fileList.length !== 0) {
        this.form.fileList.push({
          name: this.getFileName(fileList.response.data),
          url: fileList.response.data
        });
      } else {
        this.form.fileList = [];
        this.form.fileList.push({
          name: this.getFileName(fileList.response.data),
          url: fileList.response.data
        });
      }
      
      this.$message.success(`${ fileList.name } 上传成功！`);
    },
    
    // 返回页面
    goBack() {
      this.$emit('closeDilag');
    }
  },
};
</script>

<style lang="scss">
  .popconfirmClass {
    background: rgba($color: #031a4a, $alpha: .9);
    border: 1px solid #4389f9;
    .el-popconfirm {
      // 内容样式
      .el-popconfirm__main {
        color: #4389f9;
      }
      
      // 底部按钮栏
      .el-popconfirm__action {
        padding-top: 10px;
        text-align: right;
        .el-button--default {
          background-color: #041338;
          color: #d3d3d3;
          border: 1px solid #2a5cb0;
          border-radius: 3px;
          text-align: center;
        }
        .el-button--primary {
